<template>
 <!-- <div class="flex sm:flex-col xs:flex-col md:flex-row lg:flex-row ">
  <div class="basis-3/4" style="background:red">03</div>
  <div class="basis-1/4" style="background:pink">01</div>
</div> -->
  <PageLoader
    v-if="loading"
  />
  <div class="banner-box" ref="bannerbox" style="position:relative"
    :style="{'height':`${height}px`,'background':$store.state.core.backgroundColor}"
  >
    <p v-for="element in elements" :key="element"
      @keydown="handelEvent"
      @keyup="updateText"
      @click="setActiveElement(element)"
      class="banner-item"
      :style="element.styles"
      :contentEditable="true"
    >
      {{element.text}}
    </p>
  </div>
  <br><br>
  <div class="renderedBanner" ref="renBanner"></div>
  <br><br>
  
   <!-- <button type="button" @click.prevent="savaBanner" class="mt-12 focus:outline-none text-white bg-red-700 hover:bg-red-800 focus:ring-4 focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-red-600 dark:hover:bg-red-700 dark:focus:ring-red-900">Save Banner</button> -->
  <button type="button" @click.prevent="renderPreviewHTML" class="mt-12 focus:outline-none text-white bg-red-700 hover:bg-red-800 focus:ring-4 focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-red-600 dark:hover:bg-red-700 dark:focus:ring-red-900">renderPreviewHTML</button>
   
  <div v-if="activeElement" class="conatiner mt-12">
    <div class="flex flex-col md:flex-row">
      <div class="basis-1/2 mr-2">
        <label for="countries" class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Font Size</label>
          <input v-model="activeElement.fontRatio" @change="setFont($event)" @keyup="setFont($event)" step="5" type="number"   class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Font Sizepdf">
          <br>
        <label for="countries" class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Margin-Top</label>
        <input v-model="activeElement.top" @keyup="setMargin($event)" @change="setMargin($event)" type="number"   class=" bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Margin">
         <label for="countries" class="mt-4 block mb-2 text-sm font-medium text-gray-900 dark:text-white">Text Color</label>
        <center>
          <ColorPicker
            class="mt-6"
            :color="activeElement.color"
            @color-change="updateTextColor"
            default-format="rgb"
          />
        </center>
      </div>
      <div class="basis-1/2">
       <label for="countries" class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Font</label>
          <select v-model="font" @change="changeFont" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500">
            <option selected>Choose a font</option>
            <option v-for="thisFont in allFonts" :key="thisFont" :value="thisFont">{{thisFont}}</option>
          </select>
       <label for="countries" class=" mt-24 block mb-2 text-sm font-medium text-gray-900 dark:text-white">Banner Background</label>
          <center>
          <ColorPicker
            class="mt-6"
            :color="$store.state.core.backgroundColor"
            @color-change="updateBackgroundColor"
            default-format="rgb"
          />
        </center>
      </div>
      <!-- <div class="basis-1/2">03</div> -->
    </div>
  </div>

</template>
<script>
/* eslint-disable */
import { ColorPicker } from 'vue-accessible-color-picker'
import { DIMENSIONS, ALL_BANNERS } from '@/constants'
import * as htmlToImage from 'html-to-image';
import html2pdf from "html2pdf.js";
import PageLoader from '@/components/PageLoader.vue' 
  export default {
    components:{
      ColorPicker,
      PageLoader
    },
    data() {
      return {
        json:null,
        height:null,
        width:null,
        elements: [],
        activeElement:null,
        font:'Shojumaru',
        allFonts:['takeaway', 'monkeyfury','chinesef','chinesef2','Shojumaru','HanyiSentyBrush','HanyiSentyPastel'],
        loading:false,
        render:false,
        renderHeight:null
      }
    },
    mounted() {
      this.setDimensions()
      // this.updateBanner()
      let banner = this.$store.state.core.banner
      let formatId = banner.format
      let json = ALL_BANNERS[formatId]
      this.json = json
      json.forEach((element,index) => {
        let ratialFontSize = this.height/element.fontRatio
        element.styles['font-size'] =`${ratialFontSize}rem`
        element.styles['font-family'] = this.font
        element.styles['top'] = `${element.top}%`
        element.fontSize = Number(element.styles['font-size'].split("r")[0])
        element.marginTop = Number(element.styles['margin-top'].split("r")[0])
        element.color = element.styles.color
        console.log(element)
      });
      this.elements = json
      this.activeElement = this.elements[0]
    },
    methods: {
      updateBanner() {

      },
      renderPreviewHTML() {
        let banner = this.$store.state.core.banner
        let dimensionsId = banner.dimensions
        let dimensions = DIMENSIONS[dimensionsId]
        let canvasWrapper = 700
        let ratio = canvasWrapper/dimensions.width
        let calcHeight = ratio * dimensions.height
        this.renderHeight = calcHeight

        let html = ''
        html += `<div class="renverPreview" style="height:${this.renderHeight}px;width:${canvasWrapper}px;background:${this.$store.state.core.backgroundColor}">`
        this.elements.forEach((ele)=>{
          let ratialFontSize = this.renderHeight/ele.fontRatio
          html += `<p  class="banner-item" 
          style="font-size: ${ratialFontSize}rem; color: ${ele.styles.color}; font-family: ${this.font}; top: ${ele.top}%;">${ele.text}</p>`
        })
        html +=  '</div>';
        this.$refs.renBanner.innerHTML =  html 
        console.log(this.elements)
      },
      handelEvent(event){
        if(event.key == 'Enter'){
            event.preventDefault()
         }
      },
      changeFont() {
        console.log(this.font)
        this.json.forEach((element)=>{
          element.styles['font-family'] = this.font
        })
      },
      setActiveElement(element) {
        this.activeElement = element
      },
      setFont(event) {
        this.elements = this.elements.map((ele)=>{
          if(ele.index == this.activeElement.index){
            ele.fontRatio = event.target.value
            let ratialFontSize = this.height/ele.fontRatio
            ele.styles['font-size'] =`${ratialFontSize}rem`
          }
          return ele
        })
      },
      setMargin(event) {
        this.elements = this.elements.map((ele)=>{
          if(ele.index == this.activeElement.index){
            ele.styles['top'] =`${event.target.value}%`
          }
          return ele
        })
        this.activeElement.styles['margin-top'] = `${this.activeElement.marginTop}rem`
      },
      updateText(event) {
        // console.log(event.target.innerText)
        this.elements = this.elements.map((ele)=>{
          if(ele.index == this.activeElement.index){
            ele.text = event.target.innerText
          }
          return ele
        })
      },
      updateBackgroundColor(data) {
        this.$store.commit("backgroundColor",data.cssColor)
      },
      updateTextColor(data) {
        this.activeElement.styles.color = data.cssColor
      },
      setDimensions() {
        let banner = this.$store.state.core.banner
        let dimensionsId = banner.dimensions
        let dimensions = DIMENSIONS[dimensionsId]
        let canvasWrapper = this.$refs.bannerbox.clientWidth
        let ratio = canvasWrapper/dimensions.width
        let calcHeight = ratio * dimensions.height
        this.height = calcHeight
      },
      getNum(str) {
        return str.match(/(\d+)/);
      },
      preview() {
        this.loading = true
        htmlToImage.toPng(this.$refs.bannerbox)
          .then( (dataUrl) => {
            this.loading = false
            console.log(dataUrl)
        });
      },
      savaBanner() {
        // let bannerElements = JSON.stringify(this.elements) 
        let banner = this.$store.state.core.banner
        let dimensionsId = banner.dimensions
        let dimensions = DIMENSIONS[dimensionsId]
        let toSaveBanner = {
          elements:this.elements,
          background:this.$store.state.core.backgroundColor,
          dimensions:dimensions
        }
        console.log(JSON.stringify(toSaveBanner))
       this.$store.commit("savedBanner",toSaveBanner)
      },
      exportToPDF() {
        html2pdf(this.$refs.bannerbox, {
          filename: "i-was-html.pdf",
          jsPDF:{
            orientation:'landscape'
          }
        });
      },
    }
  }
</script>
<style>
.banner-item{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: clip;
  cursor: pointer;
  position: absolute;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
}
.banner-box{
  display:grid;
  text-align: center;
}
.renverPreview{
 display:grid;
  text-align: center;
  position:relative
}
</style>