<template>
 <div class="relative mx-auto sm:max-w-xl md:max-w-full  mt-8 mb-8" style="max-width:80%">
   <div class="renderedBanner" ref="renBanner"></div>
  </div>
</template>
<script>
/* eslint-disable */
import { DIMENSIONS } from '@/constants'

export default {
  data() {
    return {
      renderHeight:null,
      elements:{"elements":[{"index":1,"text":"感謝舊雨新知 歡迎再度光臨 敬請期待","fontRatio":150,"top":3,"styles":{"font-size":"2.932rem","margin-top":"0px","color":"rgb(81.66 205.13 44.08 / 1)","font-family":"Shojumaru","top":"3%"},"fontSize":2.932,"marginTop":null,"color":"rgb(234.02 229.29 229.29 / 1)"},{"index":2,"text":"大匠豬排店遷移啟事","fontRatio":60,"top":30,"styles":{"font-size":"7.33rem","margin-top":"0px","color":"rgb(205.13 67.59 67.59 / 1)","font-family":"Shojumaru","top":"30%"},"fontSize":7.33,"marginTop":null,"color":"rgb(234.02 229.29 229.29 / 1)"},{"index":3,"text":"新地址:台北市中山北路 12段號","fontRatio":150,"top":80,"styles":{"font-size":"2.932rem","margin-top":"0px","color":"rgb(22.38 1.39 1.39 / 1)","font-family":"Shojumaru","top":"80%"},"fontSize":2.932,"marginTop":null,"color":"rgb(234.02 229.29 229.29 / 1)"}],"background":"rgb(48.11 24.9 223.55 / 0.67)","dimensions":{"height":90,"width":300}}
    }
  },
  mounted() {
    // this.elements = this.$store.state.core.savedBanner
    this.renderPreviewHTML()
  },
  methods: {
    renderPreviewHTML() {
      let dimensions = this.elements.dimensions
      let canvasWrapper = this.$refs.renBanner.clientWidth
      let ratio = canvasWrapper/dimensions.width
      let calcHeight = ratio * dimensions.height
      this.renderHeight = calcHeight

      let html = ''
      html += `<div class="renverPreview" style="height:${this.renderHeight}px;width:${canvasWrapper}px;background-color:${this.elements.background}">`
      this.elements.elements.forEach((ele)=>{
        let ratialFontSize = this.renderHeight/ele.fontRatio
        let fontFamily = ele.styles['font-family']
        html += `<p  class="banner-item" 
        style="font-size: ${ratialFontSize}rem; color: ${ele.styles.color}; font-family: ${fontFamily}; top: ${ele.top}%;">${ele.text}</p>`
      })
      html +=  '</div>';
      this.$refs.renBanner.innerHTML =  html 
      console.log(this.elements)
    },
  }
}
</script>
 <style>
html {
  -webkit-print-color-adjust: exact;
}
