<template>
  <div class="text-center">
    <div href="#" class="py-8 px-4 mx-auto block max-w-sm p-6 bg-white border border-gray-200 rounded-lg shadow hover:bg-gray-100 dark:bg-gray-800 dark:border-gray-700 dark:hover:bg-gray-700">
    <h5 class="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">Please enter your order-id</h5>
    <input step="5" type="text" v-model="orderId"   class="mt-2 bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="order-id">
     <button type="button" @click.prevent="getOrderDetails" class="mt-2 focus:outline-none text-white bg-red-700 hover:bg-red-800 focus:ring-4 focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-red-600 dark:hover:bg-red-700 dark:focus:ring-red-900">show order</button>
</div>
  </div>

   <div class="relative mx-auto sm:max-w-xl md:max-w-full  mt-8 mb-8" style="max-width:80%">
   <div class="renderedBanner" ref="renBanner"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      orderId:null,
      orderDetails:null,
      renderHeight:null,
      elements:{}
    }
  }, 
  methods: {
  async getOrderDetails() {
      try{
        let {data} = await this.axios.get(`orders/${this.orderId}`)
        console.log(data)
        this.elements = data.bannerJSON
        this.renderPreviewHTML()
      }catch(e) {
        console.log(e)
        // let { data : { message } } = e.response
        // this.error = message
        this.$notify({ title: "Error", text: 'there was an error' });
      }
    },
    renderPreviewHTML() {
      let dimensions = this.elements.dimensions
      let canvasWrapper = this.$refs.renBanner.clientWidth
      let ratio = canvasWrapper/dimensions.width
      let calcHeight = ratio * dimensions.height
      this.renderHeight = calcHeight

      let html = ''
      html += `<div class="renverPreview" style="height:${this.renderHeight}px;width:${canvasWrapper}px;background-color:${this.elements.background}">`
      this.elements.elements.forEach((ele)=>{
        let ratialFontSize = this.renderHeight/ele.fontRatio
        let fontFamily = ele.styles['font-family']
        html += `<p  class="banner-item" 
        style="font-size: ${ratialFontSize}rem; color: ${ele.styles.color}; font-family: ${fontFamily}; top: ${ele.top}%;">${ele.text}</p>`
      })
      html +=  '</div>';
      this.$refs.renBanner.innerHTML =  html 
      console.log(this.elements)
    },
  }
}
</script>
